import { Anchor, Button, FormGroup, Input, Label } from "@doar/components";
import {
    StyledBottomText,
    StyledDesc,
    StyledDivider,
    StyledLabelWrap,
    StyledTitle,
    StyledWrap,
} from "./style";
import { SubmitHandler, useForm } from "react-hook-form";

import Cookies from "js-cookie";
import { LoginGoolge } from "../../utill/google";
import { StyledInput } from "@doar/components/src/forms/form-elements/input/style";
import { hasKey } from "@doar/shared/methods";
import moment from "moment";
import { useNavigate } from "react-router-dom";
import { useState } from "react";

interface IFormValues {
    companyUUID: string;
    companyNm: string;
    password: string;
}

const SigninForm = () => {
    const navigate = useNavigate();
    const [userId, setUserId] = useState<string>("");
    const [password, setPassword] = useState<string>("");
    const [sheetUUID, setSheetUUID] = useState<string>("");
    const login = async () => {
        if (!sheetUUID) {
            alert("회사 고유 아이디를 입력해주세요.");
            return;
        }
        if (!userId) {
            alert("회사명을 입력해주세요.");
            return;
        }
        if (!password) {
            alert("비밀번호를 입력해주세요.");
            return;
        }
        const resultGoogle: { result: string; message: string } | undefined =
            await LoginGoolge({
                sheetId: sheetUUID,
                password,
                userId,
            });

        if (resultGoogle?.result === "SUCCESS") {
            Cookies.set(
                "CLUST_PARTNERS_TOKEN",
                JSON.stringify({
                    sheetUUID,
                    data: moment().format("YYYY-MM-DD"),
                })
            );

            alert(
                `로그인 유효기간은 ${moment().format(
                    "YYYY년 MM월 DD일 23시 59분 59초"
                )}까지 입니다.`
            );
            navigate("/taxation");
        } else {
            alert(
                resultGoogle?.message ||
                    "로그인을 시도하는 도중 오류가 발생하였습니다."
            );
        }
    };

    return (
        <StyledWrap>
            <StyledTitle>로그인</StyledTitle>
            <StyledDesc>회사명과 관리자 비밀번호를 입력해주세요.</StyledDesc>
            <form action="#" onSubmit={login} noValidate>
                <FormGroup mb="20px">
                    <Label display="block" mb="5px" htmlFor="companyUUID">
                        회사 고유 아이디
                    </Label>
                    <StyledInput
                        type={"text"}
                        className={"form-control"}
                        id={"companyUUID"}
                        name={"sheetUUID"}
                        onChange={(e) => {
                            setSheetUUID(e.target.value);
                        }}
                        value={sheetUUID}
                    />
                </FormGroup>
                <FormGroup mb="20px">
                    <Label display="block" mb="5px" htmlFor="companyNm">
                        회사명
                    </Label>
                    <StyledInput
                        type={"text"}
                        className={"form-control"}
                        id={"companyNm"}
                        name={"userId"}
                        onChange={(e) => {
                            setUserId(e.target.value);
                        }}
                        value={userId}
                    />
                </FormGroup>
                <FormGroup mb="20px">
                    <StyledLabelWrap>
                        <Label display="block" mb="5px" htmlFor="password">
                            비밀번호
                        </Label>
                    </StyledLabelWrap>
                    <StyledInput
                        type={"text"}
                        className={"form-control"}
                        id={"password"}
                        name={"password"}
                        onChange={(e) => {
                            setPassword(e.target.value);
                        }}
                        value={password}
                    />
                </FormGroup>
                <Button
                    color="dark"
                    onClick={() => {
                        login();
                    }}
                    fullwidth
                >
                    로그인
                </Button>
                <StyledBottomText>
                    비밀번호가 기억나지 않으시나요{" "}
                    <span className="copright-link">
                        <a
                            href="http://www.clust.kr/pc/sub5.jsp"
                            target="_blank"
                            rel="noopener noreferrer"
                        >
                            고객센터
                        </a>
                    </span>
                </StyledBottomText>
            </form>
        </StyledWrap>
    );
};

export default SigninForm;
