import axios from 'axios';
import moment from 'moment';

interface Props {
    sheetId?: string;
}
const getUserRowData = async (props: Props) => {
    const { sheetId } = props;
    if (!sheetId) {
        alert('회원고유값을 입력해주세요.');
        return;
    }
    const baseUrl = `https://sheets.googleapis.com/v4/spreadsheets/19ARizlXuVh9UY6WCLdSu5nf10Qs3YEGSoUGtDKxPopk/values:batchGet?key=AIzaSyD5wHc-n33_KurzoJtuVcUf-1U5mIbGFgk&ranges=${encodeURI(
        '거래처명'
    )}&ranges=${encodeURI('세무!B1')}&ranges=${encodeURI('패스워드')}&ranges=${encodeURI(
        '세무!A3:D7'
    )}&ranges=${encodeURI('가입!A1:B')}&ranges=${encodeURI('탈퇴!A1:B')}&ranges=${encodeURI(
        '삭제!A1:B'
    )}&majorDimension=COLUMNS`;
    // const baseUrl = `https://sheets.googleapis.com/v4/spreadsheets/19ARizlXuVh9UY6WCLdSu5nf10Qs3YEGSoUGtDKxPopk/values:batchGet?key=AIzaSyD5wHc-n33_KurzoJtuVcUf-1U5mIbGFgk&ranges=${encodeURI(
    //     "세무!A3:D7"
    // )}&ranges=${encodeURI("가입!A1:B")}&ranges=${encodeURI(
    //     "탈퇴!A1:B"
    // )}&ranges=${encodeURI("삭제!A1:B")}&majorDimension=COLUMNS`;
    // const baseUrl = `https://sheets.googleapis.com/v4/spreadsheets/19ARizlXuVh9UY6WCLdSu5nf10Qs3YEGSoUGtDKxPopk/values:batchGet?key=AIzaSyBNOHMa5aSkVY0ToJIa8---o0bO8BoHloc&ranges=${encodeURI(
    //     "설치!A1:B"
    // )}&ranges=${encodeURI("가입!A1:B")}&ranges=${encodeURI(
    //     "탈퇴!A1:B"
    // )}&ranges=${encodeURI("삭제!A1:B")}&majorDimension=COLUMNS`;

    const { data }: { data: any } = await axios.get(`${baseUrl}`);
    console.log({ data });

    let totalInsert = 0;
    let uninstallation = 0;
    let join = 0;
    let leave = 0;
    const defaultXY = [
        { x: '6/13일전', y: 0 },
        { x: '5/12일전', y: 0 },
        { x: '4/11일전', y: 0 },
        { x: '3/10일전', y: 0 },
        { x: '2/9일전', y: 0 },
        { x: '1/8일전', y: 0 },
        { x: '오늘/7일전', y: 0 },
    ];
    const thisUninstallationWeeks = [...defaultXY];
    const lastUninstallationWeeks = [...defaultXY];
    const thisLeaveWeeks = [...defaultXY];
    const lastLeaveWeeks = [...defaultXY];
    const thisJoinWeeks = [...defaultXY];
    const lastJoinWeeks = [...defaultXY];
    const thisTotalInsertWeeks = [...defaultXY];
    const lastTotalInsertWeeks = [...defaultXY];

    if (data) {
        if (data && data?.valueRanges?.length > 0) {
            const thisWeekStartDate = moment().subtract(6, 'day').format('YYYY-MM-DD');
            const thisWeekEndDate = moment().format('YYYY-MM-DD');
            const lastWeekStartDate = moment().subtract(13, 'day').format('YYYY-MM-DD');
            const lastWeekEndDate = moment().subtract(7, 'day').format('YYYY-MM-DD');

            const items = data;
            items?.valueRanges.map((item: any, index: number) => {
                if (item.range.indexOf('설치') !== -1) {
                    item.values[0].map((totalInsertList: any, totalInsertIndex: number) => {
                        if (totalInsertIndex > 0) {
                            totalInsert += Number(item.values[1][totalInsertIndex]);
                            console.log(
                                moment(totalInsertList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            );
                            console.log(totalInsertList);
                            console.log(thisWeekStartDate);

                            if (
                                moment(totalInsertList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                console.log(thisLeaveWeeks[6]);

                                const dayCount = moment(
                                    moment(totalInsertList.replaceAll(' ', '')).format('YYYY-MM-DD')
                                ).diff(thisWeekStartDate, 'days');
                                thisTotalInsertWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][totalInsertIndex]),
                                };
                            }

                            if (
                                moment(totalInsertList.replaceAll(' ', '')).isBetween(
                                    lastWeekStartDate,
                                    lastWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                const dayCount = moment(
                                    moment(totalInsertList.replaceAll(' ', '')).format('YYYY-MM-DD')
                                ).diff(lastWeekStartDate, 'days');
                                lastTotalInsertWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][totalInsertIndex]),
                                };
                            }
                        }
                    });
                } else if (item.range.indexOf('삭제') !== -1) {
                    item.values[0].map((uninstallationList: any, uninstallationIndex: number) => {
                        if (uninstallationIndex > 0) {
                            uninstallation += Number(item.values[1][uninstallationIndex]);
                            if (
                                moment(uninstallationList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                // const thisUninstallationWeek = {};
                                // thisUninstallationWeek[
                                //   moment(uninstallationList.replaceAll(" ", "")).format(
                                //     "YYYY-MM-DD"
                                //   )
                                // ] = Number(item.values[1][uninstallationIndex]);
                                const dayCount = moment(
                                    moment(uninstallationList.replaceAll(' ', '')).format('YYYY-MM-DD')
                                ).diff(thisWeekStartDate, 'days');
                                thisUninstallationWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][uninstallationIndex]),
                                };
                            }
                            if (
                                moment(uninstallationList.replaceAll(' ', '')).isBetween(
                                    lastWeekStartDate,
                                    lastWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                const dayCount = moment(
                                    moment(uninstallationList.replaceAll(' ', '')).format('YYYY-MM-DD')
                                ).diff(lastWeekStartDate, 'days');
                                lastUninstallationWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][uninstallationIndex]),
                                };
                            }
                        }
                    });
                } else if (item.range.indexOf('가입') !== -1) {
                    item.values[0].map((joinList: any, joinIndex: number) => {
                        if (joinIndex > 0) {
                            join += Number(item.values[1][joinIndex]);
                            console.log(
                                moment(joinList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            );
                            console.log(joinList);
                            console.log(thisWeekStartDate);

                            if (
                                moment(joinList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                console.log(thisLeaveWeeks[6]);

                                const dayCount = moment(moment(joinList.replaceAll(' ', '')).format('YYYY-MM-DD')).diff(
                                    thisWeekStartDate,
                                    'days'
                                );
                                thisJoinWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][joinIndex]),
                                };
                            }

                            if (
                                moment(joinList.replaceAll(' ', '')).isBetween(
                                    lastWeekStartDate,
                                    lastWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                const dayCount = moment(moment(joinList.replaceAll(' ', '')).format('YYYY-MM-DD')).diff(
                                    lastWeekStartDate,
                                    'days'
                                );
                                lastJoinWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][joinIndex]),
                                };
                            }
                        }
                    });
                } else if (item.range.indexOf('탈퇴') !== -1) {
                    item.values[0].map((leaveList: any, leaveIndex: number) => {
                        if (leaveIndex > 0) {
                            leave += Number(item.values[1][leaveIndex]);
                            console.log(
                                moment(leaveList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            );
                            console.log(leaveList);
                            console.log(thisWeekStartDate);

                            if (
                                moment(leaveList.replaceAll(' ', '')).isBetween(
                                    thisWeekStartDate,
                                    thisWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                console.log(thisLeaveWeeks[6]);

                                const dayCount = moment(
                                    moment(leaveList.replaceAll(' ', '')).format('YYYY-MM-DD')
                                ).diff(thisWeekStartDate, 'days');
                                thisLeaveWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][leaveIndex]),
                                };
                            }

                            if (
                                moment(leaveList.replaceAll(' ', '')).isBetween(
                                    lastWeekStartDate,
                                    lastWeekEndDate,
                                    undefined,
                                    '[]'
                                )
                            ) {
                                const dayCount = moment(
                                    moment(leaveList.replaceAll(' ', '')).format('YYYY-MM-DD')
                                ).diff(lastWeekStartDate, 'days');
                                lastLeaveWeeks[dayCount] = {
                                    x: dayCount === 6 ? '오늘/7일전' : `${6 - dayCount}/${6 - dayCount + 7}일전`,
                                    y: Number(item.values[1][leaveIndex]),
                                };
                            }
                        }
                    });
                }
            });
        }
    }
    const returnData = {
        totalInsert,
        uninstallation,
        join,
        leave,
        defaultXY,
        thisUninstallationWeeks,
        lastUninstallationWeeks,
        thisLeaveWeeks,
        lastLeaveWeeks,
        thisJoinWeeks,
        lastJoinWeeks,
        thisTotalInsertWeeks,
        lastTotalInsertWeeks,
    };
    const result = {
        result: data ? 'SUCCESS' : 'FAIL',
        data: returnData,
    };
    return result;
};
interface LoginProps {
    sheetId: string;
    userId: string;
    password: string;
}
const LoginGoolge = async (props: LoginProps) => {
    const { sheetId, userId, password } = props;
    if (!sheetId || !userId || !password) {
        return;
    }
    const baseUrl = `https://sheets.googleapis.com/v4/spreadsheets/${sheetId}/values:batchGet?key=AIzaSyD5wHc-n33_KurzoJtuVcUf-1U5mIbGFgk&ranges=${encodeURI(
        '고객정보!B3:B4'
    )}&majorDimension=COLUMNS`;

    try {
        const { data }: { data: any } = await axios.get(`${baseUrl}`);
        console.log({ data });

        if (data?.valueRanges?.length > 0 && data?.valueRanges[0]?.values?.length > 0) {
            let userIdInSheet = data?.valueRanges[0]?.values[0]?.[0];
            let passwordInSheet = data?.valueRanges[0]?.values[0]?.[1];
            console.log(userIdInSheet);
            console.log(passwordInSheet);
            if (userIdInSheet !== userId || passwordInSheet !== password) {
                const result = {
                    result: 'FAIL',
                    message: '회사명과 비밀번호를 확인해주세요.',
                };
                return result;
            }

            const result = {
                result: data ? 'SUCCESS' : 'FAIL',
                message: '',
            };
            return result;
        } else {
            const result = {
                result: 'FAIL',
                message: '회사 고유 아이디를 확인해주세요.',
            };
            return result;
        }
    } catch (error) {
        console.log(error);

        const result = {
            result: 'FAIL',
            message: '회사 고유 아이디를 확인해주세요.',
        };
        return result;
    }
};

export { getUserRowData, LoginGoolge };
